'use client'

import { useEffect, useState } from 'react'
import { LiveFeed } from '@/components/dashboard/LiveFeed'
import { QuickActions } from '@/components/dashboard/QuickActions'
import { useCharacterStore } from '@/lib/character-store'
import { createClient } from '@/lib/supabase/client'

export default function DashboardPage() {
  const { character } = useCharacterStore()
  const [username, setUsername] = useState<string>('Player')
  const supabase = createClient()

  useEffect(() => {
    const getUsername = async () => {
      const { data: { user } } = await supabase.auth.getUser()
      
      if (user?.user_metadata?.username) {
        setUsername(user.user_metadata.username)
      } else {
        setUsername(user?.email?.split('@')[0] || 'Player')
      }
    }
    
    getUsername()
  }, [supabase])

  if (!character) {
    return (
      <div className="flex items-center justify-center h-screen bg-[#0f0f0f]">
        <div className="text-center">
          <div className="inline-block h-8 w-8 animate-spin rounded-full border-2 border-solid border-[#5cb85c] border-r-transparent mb-4"></div>
          <p className="text-[#888] uppercase text-xs tracking-widest">Loading...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-4 fade-in max-w-7xl mx-auto">
      {/* User Header */}
      <div className="ls-section">
        <div className="ls-section-header">{username} • Downtown</div>
        <div className="ls-section-content">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="ls-stat-card">
              <div className="ls-stat-icon">💰</div>
              <div className="ls-stat-info">
                <div className="ls-stat-label">Cash</div>
                <div className="ls-stat-value text-success">
                  ${character.money.toLocaleString()}
                </div>
              </div>
            </div>

            <div className="ls-stat-card">
              <div className="ls-stat-icon">⚡</div>
              <div className="ls-stat-info">
                <div className="ls-stat-label">Energy</div>
                <div className="ls-stat-value text-warning">
                  {character.energy} / {character.maxEnergy}
                </div>
                <div className="ls-bar">
                  <div 
                    className="ls-bar-fill bg-[#f0ad4e]"
                    style={{ width: `${(character.energy / character.maxEnergy) * 100}%` }}
                  />
                </div>
              </div>
            </div>

            <div className="ls-stat-card">
              <div className="ls-stat-icon">❤️</div>
              <div className="ls-stat-info">
                <div className="ls-stat-label">Health</div>
                <div className="ls-stat-value text-success">
                  {character.health} / {character.maxHealth}
                </div>
                <div className="ls-bar">
                  <div 
                    className="ls-bar-fill bg-[#5cb85c]"
                    style={{ width: `${(character.health / character.maxHealth) * 100}%` }}
                  />
                </div>
              </div>
            </div>

            <div className="ls-stat-card">
              <div className="ls-stat-icon">⭐</div>
              <div className="ls-stat-info">
                <div className="ls-stat-label">Level</div>
                <div className="ls-stat-value text-info">{character.level}</div>
                <div className="ls-bar">
                  <div 
                    className="ls-bar-fill bg-[#5bc0de]"
                    style={{ width: `${(character.xp / character.xpNeeded) * 100}%` }}
                  />
                </div>
                <div className="ls-bar-text">
                  <span>{character.xp} XP</span>
                  <span>{character.xpNeeded} XP</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Character Info */}
      <div className="ls-section">
        <div className="ls-section-header">Character</div>
        <div className="ls-section-content">
          <div className="ls-info-row">
            <span className="ls-info-label">Career:</span>
            <span className="ls-info-value">{character.jobTitle || 'Unemployed'}</span>
          </div>
          <div className="ls-info-row">
            <span className="ls-info-label">Education:</span>
            <span className="ls-info-value">{character.education || 'None'}</span>
          </div>
          <div className="ls-info-row">
            <span className="ls-info-label">Reputation:</span>
            <span className="ls-info-value">{character.reputation}</span>
          </div>
          <div className="ls-info-row">
            <span className="ls-info-label">Crimes Committed:</span>
            <span className="ls-info-value text-danger">{character.crimesCommitted}</span>
          </div>
        </div>
      </div>

      {/* Main Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
        <div className="lg:col-span-2">
          <QuickActions />
        </div>
        <div className="lg:col-span-1">
          <LiveFeed />
        </div>
      </div>

      {/* Combat Stats */}
      <div className="ls-section">
        <div className="ls-section-header">Combat Stats</div>
        <div className="ls-section-content">
          <div className="ls-info-row">
            <span className="ls-info-label">Strength</span>
            <div className="flex-1 mx-4">
              <div className="ls-bar">
                <div 
                  className="ls-bar-fill bg-[#d9534f]"
                  style={{ width: `${character.strength}%` }}
                />
              </div>
            </div>
            <span className="ls-info-value">{character.strength}</span>
          </div>
          <div className="ls-info-row">
            <span className="ls-info-label">Defense</span>
            <div className="flex-1 mx-4">
              <div className="ls-bar">
                <div 
                  className="ls-bar-fill bg-[#5bc0de]"
                  style={{ width: `${character.defense}%` }}
                />
              </div>
            </div>
            <span className="ls-info-value">{character.defense}</span>
          </div>
          <div className="ls-info-row">
            <span className="ls-info-label">Speed</span>
            <div className="flex-1 mx-4">
              <div className="ls-bar">
                <div 
                  className="ls-bar-fill bg-[#5cb85c]"
                  style={{ width: `${character.speed}%` }}
                />
              </div>
            </div>
            <span className="ls-info-value">{character.speed}</span>
          </div>
          <div className="ls-info-row">
            <span className="ls-info-label">Dexterity</span>
            <div className="flex-1 mx-4">
              <div className="ls-bar">
                <div 
                  className="ls-bar-fill bg-[#f0ad4e]"
                  style={{ width: `${character.dexterity}%` }}
                />
              </div>
            </div>
            <span className="ls-info-value">{character.dexterity}</span>
          </div>
        </div>
      </div>
    </div>
  )
}